import "./styles.css";
var colour = document.getElementById("btn1");
var bg_color = document.getElementById("circle");
var shape = document.getElementById("btn2");
var shape_change = document.getElementById("shape");

colour.addEventListener("click", function () {
  if (bg_color.style.backgroundColor === "red") {
    bg_color.style.backgroundColor = "blue";
  } else {
    bg_color.style.backgroundColor = "red";
  }

  if (shape_change.style.height === "0px") {
    if (bg_color.style.backgroundColor === "red") {
      shape_change.style.borderTop = "170px solid red";
    } else {
      shape_change.style.borderTop = "170px solid blue";
    }
  }
});

shape.addEventListener("click", function () {
  shape_change.style.height = "0px";
  shape_change.style.width = "0px";
  shape_change.style.borderLeft = "170px solid white";
  if (bg_color.style.backgroundColor === "red") {
    shape_change.style.borderTop = "170px solid red";
  } else {
    shape_change.style.borderTop = "170px solid blue";
  }
});
